/* RESETS */
*,
*::before,
*::after {
    box-sizing: border-box;
}
*:focus {
    outline: 3px dashed #228bec;
    outline-offset: 0;
}
html {
    font: 62.5% / 1.15 sans-serif;
}
h1,
h2 {
    margin-bottom: 0;
}
ul {
    list-style: none;
    padding: 0;
}
button {
    border: none;
    margin: 0;
    padding: 0;
    width: auto;
    overflow: visible;
    background: transparent;
    color: inherit;
    font: inherit;
    line-height: normal;
    -webkit-font-smoothing: inherit;
    -moz-osx-font-smoothing: inherit;
    appearance: none;
}
button::-moz-focus-inner {
    border: 0;
}
button,
input,
optgroup,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}
button,
input {
    overflow: visible;
}
input[type="text"] {
    border-radius: 0;
}
body {
    width: 100%;
    max-width: 68rem;
    margin: 0 auto;
    font: 1.6rem/1.25 Arial, sans-serif;
    background-color: #f5f5f5;
    color: #4d4d4d;
}
@media screen and (min-width: 620px) {
    body {
        font-size: 1.9rem;
        line-height: 1.31579;
    }
}
/*END RESETS*/
/* GLOBAL STYLES */
.form-group > input[type="text"] {
    display: inline-block;
    margin-top: 0.4rem;
}
.btn {
    padding: 0.8rem 1rem 0.7rem;
    border: 0.2rem solid #4d4d4d;
    cursor: pointer;
    text-transform: capitalize;
}
.btn.toggle-btn {
    border-width: 1px;
    border-color: #d3d3d3;
}
.btn.toggle-btn[aria-pressed="true"] {
    text-decoration: underline;
    border-color: #4d4d4d;
}
.btn__danger {
    color: #fff;
    background-color: #ca3c3c;
    border-color: #bd2130;
}
.btn__filter {
    border-color: lightgrey;
}
.btn__primary {
    color: #fff;
    background-color: #000;
}
.btn-group {
    display: flex;
    justify-content: space-between;
}
.btn-group > * {
    flex: 1 1 49%;
}
.btn-group > * + * {
    margin-left: 0.8rem;
}
.label-wrapper {
    margin: 0;
    flex: 0 0 100%;
    text-align: center;
}
.visually-hidden {
    position: absolute !important;
    height: 1px;
    width: 1px;
    overflow: hidden;
    clip: rect(1px 1px 1px 1px);
    clip: rect(1px, 1px, 1px, 1px);
    white-space: nowrap;
}
[class*="stack"] > * {
    margin-top: 0;
    margin-bottom: 0;
}
.stack-small > * + * {
    margin-top: 1.25rem;
}
.stack-large > * + * {
    margin-top: 2.5rem;
}
@media screen and (min-width: 550px) {
    .stack-small > * + * {
      margin-top: 1.4rem;
    }
    .stack-large > * + * {
      margin-top: 2.8rem;
    }
}
.stack-exception {
    margin-top: 1.2rem;
}
/* END GLOBAL STYLES */
.searchapp {
    background: #fff;
    margin: 2rem 0 4rem 0;
    padding: 1rem;
    position: relative;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.2), 0 2.5rem 5rem 0 rgba(0, 0, 0, 0.1);
}
@media screen and (min-width: 550px) {
    .searchapp {
        padding: 4rem;
    }
}
.searchapp > * {
    max-width: 50rem;
    margin-left: auto;
    margin-right: auto;
}
.searchapp > form {
    max-width: 100%;
}
.searchapp > h1 {
    display: block;
    max-width: 100%;
    text-align: center;
    margin: 0;
    margin-bottom: 1rem;
}
.label__lg {
    line-height: 1.01567;
    font-weight: 300;
    padding: 0.8rem;
    margin-bottom: 1rem;
    text-align: center;
}
.input__lg {
    padding: 2rem;
    border: 2px solid #000;
}
.input__lg:focus {
    border-color: #4d4d4d;
    box-shadow: inset 0 0 0 2px;
}
[class*="__lg"] {
    display: inline-block;
    width: 100%;
    font-size: 1.9rem;
}
[class*="__lg"]:not(:last-child) {
    margin-bottom: 1rem;
}
@media screen and (min-width: 620px) {
    [class*="__lg"] {
        font-size: 2.4rem;
    }
}
/* Search settings styles */
.search-settings {
    padding-top: 2rem;
}
.search-input {
    display: flex;
    flex-direction: column;
    padding-bottom: 2rem;
}
.search-blacklist {
    padding-bottom: 4rem;
}
.blacklist-container {
    display: flex;
    flex-direction: column;
    padding-bottom: 0.5rem;
}
.blacklist-input {
    display: flex;
    gap: 1rem;
}
.blacklist-input-btn:hover {
    color: #90ee90;
    border-color: #90ee90;
}
.blacklist-label {
    font-size: 1.9rem;
}
@media screen and (min-width: 620px) {
    .blacklist-container {
        padding-bottom: 0.9rem;
    }
    .blacklist-label {
        font-size: 2.4rem;
    }
}
.list-of-blacklist {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin: 0;
}
.item-of-blacklist {
    display: flex;
    align-items: baseline;
    padding: 0.1rem 0.3rem 0.2rem 0.5rem;
    border: 0.2rem solid #4d4d4d;
}
.item-of-blacklist__text {
    font-size: 1.2rem;
}
.item-of-blacklist__remove-btn {
    padding: 0 0.7rem;
    padding-right: 0.3rem;
    border: none;
    font-size: 1.5rem;
    text-transform: lowercase;
}
.item-of-blacklist__remove-btn:hover {
    color: #ca3c3c;
}
@media screen and (min-width: 620px) {
    .item-of-blacklist__text {
        font-size: 1.5rem;
    }
    .item-of-blacklist__remove-btn {
        font-size: 1.9rem;
    }
}
.search-label {
    width: auto;
    font-weight: 700;
}
.search-text {
    width: 100%;
    min-height: 4.4rem;
    padding: 0.4rem 0.8rem;
    border: 2px solid #565656;
}
.search-text:focus {
    box-shadow: inset 0 0 0 2px;
}
/* User and Reviewer Sections styles */
.output-data {
    display: flex;
    column-gap: 2rem;
    align-items: center;
    width: 100%;
    padding: 1rem;
}
.output-image {
    border: 0.2rem solid #4d4d4d;
}
.output-data-text {
    overflow: hidden;
    text-overflow: ellipsis;
}
.data-item {
    margin: 0;
    padding: 0.5rem;
}
